import React, {useEffect, useState} from 'react'
import './header.css'
import axios from 'axios'

function Header(){

  const [isLogin, setIsLogin] = useState(Boolean)

  useEffect(()=>{

    // axios({
    //   method: "GET",
    //   url: 'http://127.0.0.1:8000/api/admin'
    // }).then(res=>{
    //   console.log(res)
    //   setIsLogin(res.isLogin) 
    // })
    fetch('/api/admin/')
      .then((data) => {
        setIsLogin(data.isLogin)
      });

  },[])



  return(
    <div className="header">
      <nav className="navbar">
        <div className="logo">
          <span>Apex Group</span>
        </div>

        <ul className="nav-list">
          <li className="nav-item">
            <a href="#" className="active">Form</a>
          </li>
          <li className="nav-item">
            { isLogin ?
                <a href="#" className="">
                  All Users
                </a>
              : <a href="#" className="">
                  Sigin
                </a>
            }
            
          </li>
        </ul>  
     

    </nav>
    </div>
    
  )
}

export default Header